/* Write your code here. */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: roboto;
}
.container {
  padding: 50px;
}
.img {
  width: 80vw;
  border-radius: 10px;
}
@media (max-width: 768px) {
  .img {
    width: 60vw;
  }
}

.heading {
  color: #1e293b;
  font-size: 18px;
  margin-top: 15px;
  margin-bottom: 25px;
}
@media (max-width: 768px) {
  .heading {
    font-size: 28px;
    font-weight: bold;
  }
}

.para {
  color: #64748b;
  font-size: 12px;
  margin-bottom: 15px;
}
ul {
  display: flex;
  gap: 10px;
}
@media (max-width: 768px) {
  ul {
    /* display: grid;
    grid-template-columns: auto auto auto auto;
    row-gap: 20px;
    column-gap: 0px; */
    flex-wrap: wrap;
    gap: 20px;
    width: 90%;
  }
}
